@import '../../components/dataList.scss';
@import '../../components/swiper.scss';
@import '../../components/partnerList.scss';
@import '../../components/contactArea.scss';
@import '../../components/floatButton.scss';

.index {
  &__top {
    position: relative;
    font-size: 0;
    
    &-video {
      width: 100%;
    }

    &-content {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba($color: #000000, $alpha: .3);
    }

    &-desc {
      margin-bottom: 52px;
      font-weight: bold;
    }

    &-button {
      background: #F7931E;
      color: #FFFFFF;

      &:hover {
        background: #fe9c2a;
        color: #FFFFFF;
      }

      &:focus {
        box-shadow: 0 0 0 0.2rem rgba($color: #F7931E, $alpha: .25)
      }
    }
  }

  &__data {
    background: #FFFFFF;
    box-shadow:0px 16px 16px 0px rgba(153,153,153,0.08);
  }

  &__title {
    position: relative;
    max-width: 530px;
    margin: auto;
    padding: 100px 0 128px;
    font-size:36px;
    font-weight:bold;
    color:#333333;
    text-align: center;

    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 100px;
      transform: translateX(-50%);
      display: inline-block;
      width:100px;
      height:8px;
      background:#2661E9;
    }
  }

  &__solution {
    justify-content: center;
    // margin: 0;
    
    &-item {
      padding: 60px 25px;
      background: #FFFFFF;
      text-align: center;
    }
    
    &-icon {
      width: 36px;
      height: 36px;
      object-fit: contain;
    }

    &-title {
      margin: 32px 0 30px;
      font-weight: bold;
      color: #333333;
      line-height: 1;
    }

    &-desc {
      margin-bottom: 0;
    }
  }

  &__service {
    justify-content: center;
    margin-top: 20px;
    margin-left: -30px;
    margin-right: -30px;
    padding-bottom: 76px;

    &-item {}
    
    &-link {
      display: block;
      margin-bottom: 24px;
      padding-bottom: 42px;
      background: #fff;
      border-radius: 4px;
      text-align: center;
      overflow: hidden;
      transition: all .5s;
      
      &:hover,
      &.hover {
        transform: scale(1.04);
        box-shadow:0px 10px 20px 0px rgba(102,102,102,0.2);
        text-decoration: none;

        .index__service {
          &-button {
            background: #2661E9;
            border-color: #2661E9;
            color: #FFFFFF;
          }
        }
      }
    }

    &-image {
      width: 100%;
      height: 360px;
      object-fit: cover;
    }

    &-title {
      height: 48px;
      margin-top: 45px;
      margin-bottom: 36px;
      padding: 0 30px;
      font-weight: bold;
      color: #333333;
      text-align: center;
    }

    &-button {
      min-width: 120px;
      color: #999999;
      border-color: #999999;
      transition: all .5s;
    }
  }

  &__feature {
    margin-top: 2px;
    padding-bottom: 50px;
    background: #FFFFFF;
    overflow: hidden;

    &-top {
      position: relative;
      padding-bottom: 60px;
    }

    &-more {
      position: absolute;
      bottom: 90px;
      left: 50%;
      transform: translateX(-50%);
      color: #999999;
      border-color: #999999;

      .iconfont {
        margin-left: 5px;
        font-size: 10px;
        color: #999999;
      }
    }

    &-main {
      position: relative;
      margin-top: -58px;
      padding: 58px 74px 65px 46px;
    }

    &-bg {
      position: absolute;
      top: 0;
      left: -37px;
      width: 100%;
      height: 100%;
      background: url(../../assert/images/video-bg-1.png) no-repeat 50% 0%/100%;
    }

    &-media {
      position: relative;
      width: 100%;

      video {
        width: 100%;
      }

      &.hidden {
        display: none;
      }
    }

    &-list {
      padding: 0 35px 0 15px;
    }

    &-item {
      display: flex;
      align-items: center;
      padding: 15px 30px;
      background: #F5F5F5;
      font-weight:bold;
      cursor: pointer;
      transition: all .5s;
      
      &.hover,
      &:hover {
        background: #2661E9;
        
        .index__feature {
          &-num,
          &-title {
            color: #FFFFFF;
          }
        }
      }

      & + & {
        margin-top: 12px;
      }
    }

    &-num,
    &-title {
      transition: all .5s;
    }

    &-num {
      color: #2661E9;
      font-size:40px;
    }

    &-title {
      margin-left: 30px;
      margin-bottom: 0;
      color: #333333;
      font-size:14px;
    }
  }

  &__products {
    padding-bottom: 100px;
    background: #FFFFFF;
    overflow: hidden;

    &-content {
      position: relative;
      display: flex;
      align-items: center;
      margin-top: -20px;
    }

    &-list {
      margin: 0 -7px;
      padding: 0 30px 0 0;
    }

    &-col {
      padding: 0 7px;
    }

    &-item {
      display: flex;
      align-items: center;
      margin-bottom: 14px;
      padding: 19.5px 15px;
      background: #F5F5F5;
      cursor: pointer;
      transition: all .5s;

      &.hover,
      &:hover {
        background: #2661E9;

        .index__products {
          &-title {
            color: #FFFFFF;
          }
        }
      }
    }

    &-icon {
      width: 36px;
      height: 36px;
      object-fit: contain;
    }

    &-title {
      margin-left: 20px;
      margin-bottom: 0;
      font-size:14px;
      font-weight:bold;
      color:#333333;
    }

    &-main {
      position: relative;
      padding: 55px 0 68px 35px;
    }

    &-bg {
      position: absolute;
      top: 0;
      right: -85px;
      width: 115%;
      height: 100%;
      background: url(../../assert/images/video-bg-1.png) no-repeat 0% 50%/auto 100%;
      transform: rotate(180deg);
    }

    &-media {
      position: relative;
      width: 100%;

      video {
        width: 100%;
      }

      &.hidden {
        display: none;
      }
    }
  }

  &__partner {
    padding-bottom: 80px;
  }
}

@media (max-width: 1199px) {
  .index__feature-bg,
  .index__products-bg {
    display: none;
  }
}

@media (max-width: 767px) {
  .page__banner-title,
  .page__banner-desc {
    display: none;
  }
}
